.layout {
    display: flex;
    flex-direction:column;
    height: 100%;
    width: 92%;
    padding: 3% 0;
    margin: auto;
}

.layout header{
    position: relative;
    height: auto;
    top: 2%;
    right: 7%;
}

.layout > section{
    height: 100%;
    padding-left: 50px;
    display: flex;
    justify-content: space-between;
    /* font-size: 1.2rem; */
}

.layout main{
    display: flex;
    flex-direction:column;
    height: 100%;
    width: 80%;
}

.layout aside{
    height: 100%;
    width: 100%;

}

.layout .description{
    margin: 10% 0;
    font-size: 1.7rem;
}

.layout .city-container{
    flex-grow: 2;
}

.layout .city-label{
    padding: 0 10px;
}

.layout .city-box{
    display: flex;
    justify-content: space-between;
    max-width: 80%;
    margin: 5px 0;
    margin-right: 10%;
    padding: 3%;
    border-radius: 10px;
    background-color: white;
}

.layout .clear{
    clear: left;
}

.layout input[type=text]{
    height: 100%;
    width: 50%;
    margin: auto 0;
    font-size: 1.2rem;
    border: none;
}

.layout input[type=text]:focus{
    outline: none;
}

.layout button {
    cursor: pointer;
    border: none;
    height: 100%;
    font-size: 1rem;
    border-radius: 10px;
    background-color: #046B3B;
    color: #FFFFFF;
    padding: 5px 20px;
    font-family: 'Heebo', sans-serif;
}

.layout button:hover{
    background-color: #047a5d;
}

.layout .weather-container{
    display: flex;
    justify-content: center;
    padding: 5% 0;
    align-items: center;
    flex-grow: 2;
    min-height: 100%;
    border-radius: 2px;
    background-color: #DDE4E6;
}

.layout .weather-container-2{
    width: 66%;
    margin: auto;
    border-radius: 25px;
    background-color: #27737D;
    color: white;
    text-align: center;
}

.layout .weather{
    width: 80%;
    padding: 15% 0;
    margin: auto;
    display: flex;
    flex-direction:column;
}

.layout .location{
    display: flex;
    flex-direction: column;
}

.layout .wrap-between{
    display: flex;
    justify-content: space-between;
    margin-top: 10%;
}

.layout .wrap-column{
    display: flex;
    flex-direction: column;
}

.layout .condition-title{
    font-size: 1.2rem;
    color: #E8F1F2;
}

.layout .city-name{
    font-size: 1.5rem;
    color: #E8F1F2;
    line-height: 0.8;
}

.layout .country{
    font-size: 1.3rem;
    color: #A8C6CB;
}
.layout .date{
    margin-bottom: 10%;
    color: #A8C6CB;
}

.layout .degree{
    font-size: 7rem;
    -webkit-text-stroke: thin;
    letter-spacing: -10px;
    line-height: 0.8;
}

.layout .condition{
    font-size: 1.7rem;
    color: #E8F1F2;
    line-height: 0.8;
}

.layout .weather-time{
    color: #A8C6CB;
}

/* from 520px to 767px */
@media only screen and (max-width: 767px) {

    .layout main{
        height: auto;
    }

    .layout > section{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 5%;
    }

    .layout .degree{
        letter-spacing: 0px;
    }

    .layout .location{
        margin: 5% 0;
    }

    .layout .weather-container-2{
        width: 80%;
    }

    
}